
import Box from 'vux/src/components/box/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import Vue from 'vue'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

Vue.use(AlertPlugin);
Vue.use(ConfirmPlugin);
const wx = require('weixin-js-sdk')

var AddGoodsComponent = {
  components: {
    Box,
    XInput,
    XButton,
    Divider,
    Flexbox,
    FlexboxItem,
    Datetime,
    Confirm,
    Loading
  },
  mounted:function(){
    this.$http.get('/wx/jssdk/config').then(
      function(response){
        wx.config({
          appId : response.body.appId,
          timestamp : response.body.timestamp,
          nonceStr : response.body.nonceStr,
          signature : response.body.signature,
          jsApiList : ["chooseImage","uploadImage"]
        });
        wx.ready(this.wxJsSdkInitialized);
        wx.error(this.wxJsSdkInitializeFail);
      }
      ,
      this.wxJsSdkInitializeFail);
  },
  data(){
    return{
      session:{
        bannerPictureWxServerId:'',
        title:'专场标题',
        startTime:'',
        endTime:''
      },
      showSubmitConfirm:false,
      showSubmitLoading:false,
      showWxJsSdkLoading:true,
      showUploadPictureLoading:false
    }
  },
  methods:{
    wxJsSdkInitialized(){
      this.showWxJsSdkLoading =false;
    },
    wxJsSdkInitializeFail(res){
      this.$vux.alert.show({
        title: '错误',
        content: '初始化失败！',
        onHide () {
          window.history.back();
        }
      });
    },
    choosePictureToUpload(count){
      wx.chooseImage({
        count: count, // 默认9
        //sizeType: ['original'],//原图
        sizeType: ['compressed'],//压缩图
        //sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
        success: this.uploadChoosedPicturesToWx
      });
    },
    uploadChoosedPicturesToWx(res){
      this.showUploadPictureLoading = true;
      wx.uploadImage({
        localId: res.localIds[0],
        isShowProgressTips: 0,
        success: this.saveWxServerId
      });
    },
    saveWxServerId(res){
      this.session.bannerPictureWxServerId = res.serverId;
      this.showUploadPictureLoading = false;
      this.$vux.alert.show({
        title: '提示',
        content: '上传图片成功！'
      });
    },
    setNow (timeToSet) {
      timeToSet = dateFormat(new Date(), 'YYYY-MM-DD HH:mm:ss');
    },
    submitSession(){
      this.showSubmitLoading = true;
      this.$http.post("/sessions",this.session).then(
        function(response){
          this.showSubmitLoading = false;
          this.$vux.confirm.show({
            title : '提示',
            content : '新增专场成功！',
            confirmText : "继续新增",
            cancelText : "返回首页",
            onCancel () {
              window.history.back();
            },
            onConfirm () {
              window.location.reload();
            }
          });
        },
        function(){
          this.showSubmitLoading = false;
          this.$vux.alert.show({
            title: '错误',
            content: '新增专场失败！'
          });
        }
      )
    },
    backToHome(){
      window.history.back();
    }
  }
};

export default AddGoodsComponent;



